:root{
  --font-primary: "Audiowide", sans-serif;
  --font-secondary: "Exo 2", sans-serif;
  --background-image:url(./assets/images/Body/allec-gomes-fuooELn4jyA-unsplash.jpg);
}
::-webkit-scrollbar{
  width: 10px;
}
::-webkit-scrollbar-thumb{
  background-color: var(--color-overlay);
  border-radius: 20px;
}
::-webkit-scrollbar-thumb:hover{background-color: var(--color-secondary);}
/* Start General Style */
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  -webkit-tap-highlight-color: transparent;
  font-family: var(--font-primary);
  font-weight: 600;
  color: var(--color-text);
  outline: none;
}

body{
  direction: var(--dir);
  /* background: linear-gradient(to bottom, var(--color-bg) 97%, var(--color-secondary)); */
  background-color: var(--color-bg);
  /* background-image: url(./assets/images/Body/oleg-laptev-7jQh3EiS8Bs-unsplash.jpg); */
  /* background: var(--background-image);
  background-position: 50% 100%;
  background-size: 100% 100%; */
  width: 100%;
  /* height: 100vh; */
  overflow-x: hidden;
}

.background{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /* background-color: var(--color-bg); */
  background: radial-gradient(var(--color-bg) 8px, var(--color-overlay) 30px);
  background-size: 20px 20px;
  overflow: hidden;
  z-index: -1;
}

.container{
  max-width: 90%;
  margin: auto;
  padding: 0 40px;
  overflow: hidden;
}

.box{
  /* position: absolute;
  top: 0;
  left: 0; */
  height: 100%;
  width: 100%;
  /* height: 100%; */
  min-height: 100vh;
  /* background-color: var(--color-bg); */
  padding: 7rem 0;
  padding-bottom: 4rem;
  animation: boxOpacity 1s 1;
  transition: .5s;
}

@keyframes boxOpacity {
  0%,50%{opacity: 0;}
  100%{opacity: 1;}
}

.fullscreen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: black;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 24px;
}

.close-box{
  position: fixed;
  top: 30px;
  right: 40px;
  cursor: pointer;
  z-index: 1;
}

/* End General Style */

/* Start landing Page */
.landing_page{
  height: 100vh;
  max-height:100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  backdrop-filter: blur(3px);
  overflow: hidden !important;
}
/* End landing Page */

.light{
  color:var(--color-overlay);
  background-color: var(--color-text);
}

.dark{
  color: var(--color-text);
  background-color: var(--color-bg);
}

@media screen and (max-width: 1200px) {
  .container{
    max-width: 95%;
  }

  .box{
    padding-bottom: 2rem;
  }
}

@media screen and (max-width: 768px) {
  .container{
    padding: 0 20px;
  }
}